import React, { useState } from 'react';
import { Drawer, List, ListItem, ListItemText, IconButton, Box } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { Link } from 'react-router-dom';

const SideMenu = () => {
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);

  const toggleDrawer = (open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    setIsDrawerOpen(open);
  };

  const menuItems = [
    { text: 'Home', link: '/' },
    { text: 'About', link: '/about' },
    { text: 'Projects', link: '/projects' },
    { text: 'Contact', link: '/contact' },
  ];

  const drawerList = () => (
    <Box
      sx={{ 
        width: '100%', 
        height: '100%', 
        backgroundColor: '#333', 
        color: '#fff',
        display: 'flex', 
        flexDirection: 'column',
        justifyContent: 'center', // Centers vertically
        alignItems: 'center' // Centers horizontally
      }}
      role="presentation"
      onClick={toggleDrawer(false)}
      onKeyDown={toggleDrawer(false)}
    >
      <List>
        {menuItems.map((item, index) => (
          <ListItem 
            button 
            key={index} 
            component={Link} 
            to={item.link} 
            sx={{ textAlign: 'center', margin: '20px 0' }} // Adjust spacing
          >
            <ListItemText primary={item.text} />
          </ListItem>
        ))}
      </List>
    </Box>
  );

  return (
    <div>
      <IconButton
        edge="start"
        color="inherit"
        aria-label="menu"
        onClick={toggleDrawer(true)}
      >
        <MenuIcon />
      </IconButton>
      <Drawer
        anchor="right" // Slide from the right
        open={isDrawerOpen}
        onClose={toggleDrawer(false)}
        PaperProps={{
          sx: {
            marginTop: '64px', // Positions below the header
            width: {
              xs: '100%', // 100% width for mobile
              sm: '70%',  // 70% width for tablet
              md: '30%',  // Keep 30% width for larger screens
            },
            height: 'calc(100% - 64px)' // Takes the full height minus the header
          }
        }}
      >
        {drawerList()}
      </Drawer>
    </div>
  );
};

export default SideMenu;
