import React from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { AppBar, Box, Toolbar, Button } from '@mui/material'; 
import './style/Header.css'; // Import the CSS file
import logo from '../images/logo.jpg';
import SideMenu from './SideMenu'; // Import SideMenu component

export default function Header() {
  const navigate = useNavigate();

  const handleNavigate = () => {
    navigate('/enquire');
  };

  return (
    <Box className="header-container">
      <AppBar position="static" className='header'>
        <Toolbar className="toolbar">
          <Link to='/'>
            <img src={logo} className='logo' alt="Company Logo" />
          </Link>

          {/* Menu and Enquire Button on the right */}
          <Box sx={{ display: 'flex', alignItems: 'center', marginLeft: 'auto' }}>
            <Button 
              variant="outlined" 
              className='button' 
              onClick={handleNavigate}
            >
              Enquire
            </Button>
            {/* Wrap SideMenu in a Box for spacing */}
            <Box sx={{ marginLeft: 2 }}> {/* Adjust margin here */}
              <SideMenu />
            </Box>
          </Box>
        </Toolbar>
      </AppBar>
      <div className='header-space'></div>
    </Box>
  );
}
