import React, { useState } from "react";
import Masonry from "react-masonry-css";
import './style/ImageGallery.css';

// Import images
import driveway08 from '../images/landscaping/driveway_08.jpg';
import landscaping07 from '../images/landscaping/landscaping_07.jpg';
import driveway09 from '../images/landscaping/driveway_09.jpg';
import bricklayingWalls00 from '../images/bricklaying/bricklaying_walls_00.jpg';
import fence02 from '../images/fencing/fence_02_.jpg';
import fence04 from '../images/fencing/fence_04_.jpg';
import fence1 from '../images/fencing/fence_1.jpg';
import fencing from '../images/fencing/fencing_3.jpg';
import landscape from '../images/Landscaping_2.jpg';
import slabing from '../images/landscaping/slabing_00.jpg';

const images = [
    { src: driveway08, alt: 'Driveway 08' },
    { src: landscaping07, alt: 'Landscaping 07' },
    { src: driveway09, alt: 'Driveway 09' },
    { src: bricklayingWalls00, alt: 'Bricklaying Walls 00' },
    { src: fence02, alt: 'Fence 00' },
    { src: fence04, alt: 'Fence 04' },
    { src: fence1, alt: 'Fence 1' },
    { src: fencing, alt: 'Fence 3' },
    { src: landscape, alt: 'landscaping 3' },
    { src: slabing, alt: 'landscaping 3' },
];

const ImageGallery = () => {
    const [selectedImage, setSelectedImage] = useState(null);

    const handleClick = (image) => {
        setSelectedImage(image);
    };

    const breakpointColumnsObj = {
        default: 3,
        1100: 3,
        700: 2,
    };

    return (
        <div>
            <Masonry
                breakpointCols={breakpointColumnsObj}
                className="my-masonry-grid"
                columnClassName="my-masonry-grid_column"
            >
                {images.map((image, index) => (
                    <div
                        className={`gallery-item ${selectedImage === image ? 'selected' : ''}`}
                        key={index}
                        onClick={() => handleClick(image)}
                    >
                        <img src={image.src} alt={image.alt} className="gallery-img" />
                    </div>
                ))}
            </Masonry>

            {selectedImage && (
                <div className="lightbox" onClick={() => setSelectedImage(null)}>
                    <img src={selectedImage.src} alt={selectedImage.alt} className="lightbox-img" />
                </div>
            )}
        </div>
    );
};

export default ImageGallery;
