import React, { useState } from 'react';
import { Box, TextField, Button, Typography } from '@mui/material';
import ReCAPTCHA from 'react-google-recaptcha';

const EmailForm = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: '',
  });
  const [captchaToken, setCaptchaToken] = useState(null);
  const [errors, setErrors] = useState({
    name: false,
    email: false,
    subject: false,
    message: false,
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });

    setErrors({
      ...errors,
      [name]: false,
    });
  };

  const validateEmail = (email) => {
    const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailPattern.test(email);
  };

  const handleCaptchaChange = (token) => {
    setCaptchaToken(token);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const { name, email, subject, message } = formData;

    let valid = true;
    if (!name) {
      setErrors((prev) => ({ ...prev, name: true }));
      valid = false;
    }
    if (!email || !validateEmail(email)) {
      setErrors((prev) => ({ ...prev, email: true }));
      valid = false;
    }
    if (!subject) {
      setErrors((prev) => ({ ...prev, subject: true }));
      valid = false;
    }
    if (!message) {
      setErrors((prev) => ({ ...prev, message: true }));
      valid = false;
    }
    if (!captchaToken) {
      alert('Please complete the CAPTCHA');
      valid = false;
    }

    if (valid) {
      console.log('Form Data:', formData);
      console.log('CAPTCHA Token:', captchaToken);
      // Implement form submission logic here, including sending the captchaToken to your backend
    }
  };

  return (
    <Box
      component="form"
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        maxWidth: 600,
        margin: '30px auto 0',
        padding: 2,
        boxShadow: 3,
        borderRadius: 2,
        backgroundColor: '#333',
        color: '#fff',
      }}
      onSubmit={handleSubmit}
    >
      <Typography variant="h4" component="h1" gutterBottom sx={{ color: '#fff' }}>
        Contact Us
      </Typography>

      <TextField
        fullWidth
        label="Name"
        name="name"
        value={formData.name}
        onChange={handleChange}
        error={errors.name}
        helperText={errors.name ? 'Please enter your name' : ''}
        margin="normal"
        variant="outlined"
        InputLabelProps={{
          style: { color: '#bbb' },
        }}
        InputProps={{
          style: { color: '#fff' },
          sx: {
            '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
              borderColor: '#4CAF50',
            },
          },
        }}
      />

      <TextField
        fullWidth
        label="Email"
        name="email"
        value={formData.email}
        onChange={handleChange}
        error={errors.email}
        helperText={errors.email ? 'Please enter a valid email address' : ''}
        margin="normal"
        variant="outlined"
        InputLabelProps={{
          style: { color: '#bbb' },
        }}
        InputProps={{
          style: { color: '#fff' },
          sx: {
            '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
              borderColor: '#4CAF50',
            },
          },
        }}
      />

      <TextField
        fullWidth
        label="Subject"
        name="subject"
        value={formData.subject}
        onChange={handleChange}
        error={errors.subject}
        helperText={errors.subject ? 'Please enter a subject' : ''}
        margin="normal"
        variant="outlined"
        InputLabelProps={{
          style: { color: '#bbb' },
        }}
        InputProps={{
          style: { color: '#fff' },
          sx: {
            '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
              borderColor: '#4CAF50',
            },
          },
        }}
      />

      <TextField
        fullWidth
        label="Message"
        name="message"
        value={formData.message}
        onChange={handleChange}
        error={errors.message}
        helperText={errors.message ? 'Please enter a message' : ''}
        margin="normal"
        variant="outlined"
        multiline
        minRows={4}
        maxRows={12}
        InputLabelProps={{
          style: { color: '#bbb' },
        }}
        InputProps={{
          style: { color: '#fff' },
          sx: {
            '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
              borderColor: '#4CAF50',
            },
          },
        }}
      />

      {/* reCAPTCHA Component */}
      <ReCAPTCHA
        sitekey="6LeIxAcTAAAAAJcZVRqyHh71UMIEGNQ_MXjiZKhI"  // Google's test site key
        onChange={handleCaptchaChange}
        theme="dark"
       />

      <Button
        type="submit"
        variant="contained"
        sx={{
          marginTop: 2,
          backgroundColor: '#4CAF50',
          '&:hover': {
            backgroundColor: '#388E3C',
          },
        }}
      >
        Send Message
      </Button>
    </Box>
  );
};

export default EmailForm;

