import React, { useState } from 'react';
import Masonry from 'react-masonry-css';
import './style/ProjectGallery.css'; // Ensure this is imported

const ProjectGallery = ({ images }) => {
  const [selectedImage, setSelectedImage] = useState(null);

  const handleClick = (image) => {
    setSelectedImage(image);
  };

  const breakpointColumnsObj = {
    default: 3,
    1100: 3,
    700: 2,
    500: 1,
  };

  return (
    <div>
      <Masonry
        breakpointCols={breakpointColumnsObj}
        className="my-masonry-grid"
        columnClassName="my-masonry-grid_column"
      >
        {images.map((image, index) => (
          <div
            className="gallery-item"
            key={index}
            onClick={() => handleClick(image)} // Open lightbox on click
          >
            <img src={image} alt={`Project Image ${index + 1}`} className="gallery-img" />
          </div>
        ))}
      </Masonry>

      {selectedImage && (
        <div className="lightbox" onClick={() => setSelectedImage(null)}>
          <img src={selectedImage} alt="Selected Project" className="lightbox-img" />
        </div>
      )}
    </div>
  );
};

export default ProjectGallery;


